body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hero-container{
    position: relative;
    text-align: center;
    color: white;
    width: 100%;
    height: 90vh;
}
.hero-container:after {
    content: " ";
    z-index: 10;
    display: block;
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    /* background: rgba(255, 255, 255, 0.084); */
  }
.hero-cover{
    border: blue solid 2px;
    background: black
}
.hero-container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden 
}
.hero-text{
    position: absolute;
    bottom: 30%;
    text-shadow:4px 4px 8px rgb(0, 0, 0);
    color: white;
    z-index: 1;
    text-align: center;
    width: 100%;
}
.hero-container h2{
    font-size: 50px;
    font-weight: 700

}
.hero-container p{
    font-size: 30px;
    height: auto;
    color: white
}
@media screen and (max-width: 600px){
    .hero-container{
        width: 100%;
        height: 70vh;
    }
    .hero-text{
        bottom: 5%;
        /* left: 10px; */
    }
    .hero-text h2{
        font-size: 30px
    }
    .hero-text p{
        font-size: 20px
    }
}
.footer{
    text-align: center;
    box-shadow: 0px 0px 5px rgb(0, 0, 0, .3);
    bottom: 0px;
    color: white
}
.footer-top{
    background: rgba(16, 49, 107);
    padding: 20px;
}
.footer-top-col-two{
    padding-top: 30px
}
.footer-top-col-two img{
    height: 100px;
    width: 130px;
}
.footer-bottom p{
    background: rgba(255, 193, 7);
    text-align: center;
    width: 100%;
    padding: 20px 0px;
    color: white
}
.social-icon{
    margin: 3px;
    /* box-shadow:2px 2px 5px rgb(145, 145, 145); */
    /* border-radius: 50%; */
    /* border: solid rgb(167, 7, 7) 3px */

}
 @media screen and (max-width: 700px){
    .footer-top-col-two img{
        display: none
    }
 }
.four-col{
   height: auto;
   background:white;
   padding-bottom: 50px;
}
.col-title h2{
    padding: 40px;    
    width: 100%;
    text-align: center

}
.col-title p{
    text-align: left;
    padding: 30px 20px 60px 20px;
    width: 100%;
}

.col-4{
    text-align: center;
    padding: 10px;

}

.col-4 h4{
    margin-top: 30px;
    color: rgb(193, 9, 9)
}

@media screen and (max-width:500px){
    .col-title p{
        font-size: 16px;
    }
    .col h2{
        font-size: 2em;
    }
    .col p{
        font-size: .8em;
    }
    .col-4{
        font-size: 1em;
    }
    .col-4 h4{
        font-size: 1em
    }
    .col-4 p{
        font-size: 16px
    }
}
.school-activities{
    background: #10316B;
    padding: 30px 0px;
}
.all-bubbles-container{
    margin-bottom: 80px
}
.bubbles-title{
    text-align: center;
    color: white;
    padding: 30px 0px;
}
.bubble-container{
    padding: 20px
}
.bubble{
    border-radius: 50%;
    text-align: center;
}
.bubble img{
    width: 50%;
    border-radius: 50%;
    height: auto;
    border: solid white 4px

}
.bubble p{
    font-size: 25px;
    position: absolute;
    right: 10%;
    bottom: 2%;
    font-weight: 700;
    text-shadow:-1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}
.bubble p.nat{
    color:rgb(49, 186, 22)
}
.bubble p.fut{
    color: rgb(14, 118, 245)
}
.bubble p.baile{
    color:  rgb(232, 23, 23)
}
@media screen and (max-width:600px){
    .bubble img{
        height: 125px;
        width: auto
    }
}
.school-calendar{
    padding: 6%;
}
.calendar-title h2{
    text-align: center;
    font-size: 40px;
    color: #10316B;
    width: 100%;
    padding: 0px 0px 40px 0px;
}
.dotted{
    border-right: dotted green 4px;
    border-left: dotted green 4px
}
.event{
    padding: 20px;
}
.event h3{
    color:#10316B;
    font-size: 30px;
    font-weight: 600
}
.mes{
    color: red;
    display: block;
    font-size: 22px;
    font-weight: 700
}
.fecha{
    text-align: center
}
@media screen and (max-width:772px){
    .event{
        text-align: center
    }
    .event h3{
        color:#10316B;
        font-size: 20px;
        font-weight: 600
    }
    .mes{
        color: red;
        display: block;
        font-size: 18px;
        font-weight: 700
    }

}
@media screen and (max-width:420px){
    .dotted{
        border-right: dotted rgb(255, 255, 255) 4px;
    }
}
.collage-container{
    /* border: solid blue 2px; */
    margin: 40px 0px;
    
}
.top-left-pic, .bottom-right-pic{
    /* border: solid blue 2px; */
    height: 30%;
    padding: 10px;
}
.top-right-pic img, .bottom-left-pic img{
    /* border: solid blue 2px; */
    width: 100%;
    padding: 10px;
}
.top-left-pic img, .bottom-right-pic img, .bottom-left-pic, .top-right-pic{
    width: 100%
}
.center-pic{
    min-height: 100%;
}
.center-pic img{
    padding: 10px 5px;
}
.small-pic-overlay{
    position: absolute;
    width: 97%;
    height: 36%;
    text-align: center
}
.medium-pic-overlay{
    position: absolute;
    width: 97%;
    height: 60%;
    text-align: center;
}
.large-pic-overlay{
    background: rgba(230, 187, 225, 0.639);
    top: 10px;
    right: 2px;
    position: absolute;
    width: 99%;
    height: 97%;
    text-align: center;
}
.large-pic-overlay:hover{
    transition: ease 1s;
    background: rgba(250, 131, 236);
}
.medium-pic-overlay.left{
    background: rgba(13, 157, 5, 0.646);
    bottom: 10px;
    left: 10px;
}
.medium-pic-overlay.left:hover{
    transition: ease 1s;
    background: rgba(13, 157, 5);
}
.medium-pic-overlay.right{
    background: rgba(76, 5, 157, 0.639);
    top: 10px;
    right: 10px;
}
.medium-pic-overlay.right:hover{
    transition: ease 1s;
    background: rgba(76, 5, 157);
}
.small-pic-overlay.left{
    background-color: rgba(3, 125, 255, 0.721);
    top: 10px;
    left: 10px;
}
.small-pic-overlay.left:hover{
    transition: ease 1s;
    background-color: rgb(3, 125, 255);
}
.small-pic-overlay.right{
    background-color: rgba(225, 139, 35, 0.639);
    bottom: 10px;
    right: 10px;
}
.small-pic-overlay.right:hover{
    transition: ease 1s;
    background-color: rgba(225, 139, 35);
}
.text{
    height: 100%;
    color: white;
    font-weight: 600;
}
.text h3{
    opacity: 1 !important;

}
.text.md{
    padding: 35% 0px;

}
.text.sm{
    padding: 20% 0px;
}
.text.lg{
    padding: 50% 0px;
}
@media screen and (max-width:1024px){
    .small-pic-overlay{
        width: 96%;
        height: 35%;
    }
    .medium-pic-overlay{
        width: 96%;
        height: 57%;
    }
    .small-pic-overlay.right, .medium-pic-overlay.right{
        left: 5px;
    }
    .small-pic-overlay.left, .medium-pic-overlay.left{
        left: 5px;
    }  
    .large-pic-overlay{
        right: 2px;
        position: absolute;
        width: 99%;
        height: 96%;
    }

}
@media screen and (max-width:1100px){
    .text.sm{
        padding: 5% 0px;
    }
}
@media screen and (max-width:575px){
    .collage-container-center{
        display: none;
    }
    .small-pic-overlay{
        width: 97%;
        height: 36%;
    }
    .medium-pic-overlay{
        width: 98%;
        height: 60%;
    }
    .small-pic-overlay.right, .medium-pic-overlay.right{
        left: 5px;
    }
    .small-pic-overlay.left, .medium-pic-overlay.left{
        left: 5px;
    }  
    .large-pic-overlay{
        right: 2px;
        position: absolute;
        width: 99%;
        height: 97%;
    }
    .collage-container h3{
        font-size: 30px
    }
    .collage-container p{
        font-size: 20px
    }
    .text.md{
        padding: 30% 0px;

    }
    .text.sm{
        padding: 15% 0px;
    }
}
.navigation{
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 5;
    transition: top 0.6s;

}
.navigation-hidden{
    top: -150px
}
.top-bar{
    height: 25px;
    background: rgba(255, 193, 7, 0.85);
    width: 100%

}
.toledo-logo{
    top: 0;
    position:absolute;
    z-index: 5;
    width: 100%;
    text-align: center
}
.toledo-logo img{
    position: relative;
    height: 120px;
    width: 170px
}

.navbar{
    /* height: 80px; */
    background: rgba(16, 49, 107, 0.85);
}
.navbar-brand{
    color: white;
    font-size: 26px;
}

.nav-link{
    color: white;
    font-size: 22px
}
.navbar-toggler{
    color: white
}

@media screen and (max-width:600px){
    .navbar-brand{
        display: none;
        color: rgba(16, 49, 107, 0);
    }
}
